$themes: (
 green-theme: (color: #27ae60),
 blue-theme: (color: #2980b9),
 red-theme: (color: #c0392b),
 orange-theme : (color: #d35400),
 purple-theme : (color: #8e44ad),
 midnight-theme: (color: #2c3e50),
);

@each $theme, $map in $themes {
 .#{$theme} {
  $color: map-get($map , color) !important;
  .page-content .page-head .page-title {
   color: $color;
 }
 .content svg:hover{
    color: $color;
}
.toggle-gear:hover{
 color: $color;
}
.menu .megamenu-dropdown h3 {
 color: $color;
}
}
}